import React, {useState, useEffect, useMemo} from 'react'
import axios from 'axios'
import {connect} from 'react-redux'
import TinderCard from 'react-tinder-card'
import '../assets/Swipe/Cards.css'
import Header from './Header'
import Footer from './Footer'
import Match from './Match'
import Card from './Card'
import '../assets/Swipe/Footer.css'
import * as swipe_actions from '../store/actions/swipe_action'


function Cards(props) {
    

    const [people, setPeople] = useState([]);
    const [lastDirection, setLastDirection] = useState()
    const [popup, setPopup] = useState(false)
    const [matchedPerson, setMatchedPerson] = useState()
    const alredyRemoved = []

    useEffect(() => {
      axios.get(`${process.env.REACT_APP_API_URL}/profile/list/`)
      .then(res => {
        setPeople([...people, ...res.data])
      })
    }, [])

    let users = people
    const childRefs = useMemo(() => Array(people.length).fill(0).map(i => React.createRef(), []))

    const liked_getter = (user) => {
      var liked_data
      axios.get(`${process.env.REACT_APP_API_URL}/swipe/detail/${user}/`)
      .then(res => {
        liked_data = res.data.liked
    })
    return liked_data
  }

  //   const swiped = (direction, person) => {
  //   setLastDirection(direction)
  //   alredyRemoved.push(person.name)
  //   if (direction === 'right') {
  //     props.Swipe(props.swipe_id, person.user, person.user)
  //     var liked_data = liked_getter(person.user)
  //       if (res.data.liked.includes(props.id)) {
  //         console.log('MATCH')
  //       }
  //   } else {
  //     props.Swipe(props.swipe_id, person.user, null)
  //   }
  // } 
  
    const swiped = (direction, person) => {
      setLastDirection(direction)
      alredyRemoved.push(person.name)
      if (direction === 'right') {
        props.Swipe(props.swipe_id, person.user, person.user)
        //var liked_data = liked_getter(person.user)
        axios.get(`${process.env.REACT_APP_API_URL}/swipe/detail/${person.user}/`)
        .then(res => {
          if (res.data.liked.includes(props.id)) {
            console.log('MATCH')
            setMatchedPerson(person)
            setPopup(true)
          }
        })
      } else {
        props.Swipe(props.swipe_id, person.user, null)
      }
    }

    function onClose() {
      setPopup(false)
    }

    // const onClose = () => {
    //   setPopup(false)
    // }
  
    const outOfFrame = (name) => {
      console.log(name + ' left the screen!')
      users = users.filter(person => person.name !== name)
      setPeople(users)
    }
  
    const swipe = (dir) => {
      const cardsLeft = people.filter(person => !alredyRemoved.includes(person.name))
      if (cardsLeft.length) {
        const toBeRemoved = cardsLeft[cardsLeft.length - 1].name // Find the card object to be removed
        const index = people.map(person => person.name).indexOf(toBeRemoved) // Find the index of which to make the reference to
        alredyRemoved.push(toBeRemoved) // Make sure the next card gets removed next time if this card do not have time to exit the screen
        childRefs[index].current.swipe(dir) // Swipe the card!
      }
    }



  return (
      <div>
      <Header/>
    <div className="cards">
    
      {people.map((person, index) => (
        person.user !== props.id?
        <>
        <TinderCard
        ref={childRefs[index]} className='swipe' 
        key={person.name} onSwipe={(dir) => swiped(dir, person)}
        onCardLeftScreen={() => outOfFrame(person.name)}
        preventSwipe={['up', 'down']}>
          <Card data={person}/>
        </TinderCard>
        {/* <Match person={person} onClose={onClose} popup={popup}/> */}
        </>
        : <></>
      ))}
      <Match person={matchedPerson} onClose={onClose} popup={popup}/>
    </div>
    <Footer swipe_left={() => swipe('left')} swipe_right={() => swipe('right')}/>
    </div>
  );
}


const mapstateToProps = state => {
  return {
    id: Number(state.auth_reducer.id),
    swiped: state.profile_reducer.swiped,
    liked: state.profile_reducer.liked,
    swipe_id: state.swipe_reducer.swipe_id
  }
}

const mapDispatchToProps = dispatch => {
  return {
    Swipe: (swipe_id, swiped, liked) => dispatch(swipe_actions.Swipe(swipe_id, swiped, liked))
  }
}
export default connect(mapstateToProps, mapDispatchToProps)(Cards);



