import { connect } from 'react-redux'
import WebSocketInstance from './WebSocket.js'
import React, {useState, useEffect} from 'react'
import Avatar from '@material-ui/core/Avatar';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import SpaIcon from '@material-ui/icons/Spa';
import { IconButton } from '@material-ui/core';
import {Link} from 'react-router-dom'
import '../assets/Chat/Chat.css'
import {sendMessageHandler, initializeChat} from './MessageHandler'




function ChatScreen(props) {

    const example_messages = {
        name: 'daisuke',
        content: 'hello',
        image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcS8UPpwQxL6RFeJ5tNGbpYDkOOB0OCTzq1kgw&usqp=CAU'
    }
    const [input, setInput] = useState('')
    const [messages, setMessages] = useState([])

    const fetchMessage = (fetched_messages) => {
        //.reverse()
        //...messages is no need
        console.log('fetchMessage')
        if (fetched_messages) {
            setMessages([...messages, ...fetched_messages.reverse()])
            
        }
        
    }




	const addMessage = (message) => {

        setMessages([...messages, ...message])
        console.log(messages)
    }

    useEffect(() => {
        initializeChat(fetchMessage, addMessage, WebSocketInstance, props)
        WebSocketInstance.connect(props.ChatID)
    }, [props.ChatID])




    const onSubmit = e => {
        sendMessageHandler(e, WebSocketInstance, props, input)
        console.log('submitted')
        console.log(messages)
        setInput('')
    }


    return (
        <div className='chatScreen'>
            <header className='chatScreen_header'>
            <Link to='/chatpanel'>
                    <IconButton id='chatScreen_header_icon'>
                        <ArrowBackIosIcon style={{fontSize: '30px'}}/>
                    </IconButton>
                </Link>
                <SpaIcon id='chatScreen_logo'/>
            </header>
            <div style={{textAlign: 'center'}}>
            <p1 className='chatScreen_match'>YOU MATCHED WITH ELLEN ON 10/08/20</p1>
            </div>
            <ul className='chatScreen_messages'>
            {messages.map(message => message.name !== props.name? 
                (
                <div className='chatScreen_message'>
                    <Avatar className='chatScreen_image'
                    alt={message.name} src={message.image}/>
                    <p className='chatScreen_reply'>{message.content}</p>
                    <small>
					{message.timestamp.substring(0,16)}
				    </small>
                </div>
                ) : (
                    <div className='chatScreen_message'>
                    <p className='chatScreen_sent'>{message.content}<br/>
                    <small>
					{message.timestamp.substring(0,16)}
			         </small></p>
                    
                    </div>
                ))
            }
            </ul>

        <div>
            <form className='chatScreen_input' onSubmit={onSubmit}>
                <input value={input} onChange={(e) => setInput(e.target.value)}
                 type='text' className='chatScreen_inputField' placeholder='message'/>
                <button className='chatScreen_submit' type='submit'>SEND</button>
            </form>
        </div>
    </div>
    )
}

const mapstateToProps = (state, objects) => {
    return {
        name: state.auth_reducer.name,
        ChatID: objects.match.params.ChatID

    }
}


export default connect(mapstateToProps, null)(ChatScreen)
