

export function waitForSocketConnection(callback, WebSocketInstance) {
    // const socket = this.socketRef
    //const recursion = .waitForSocketConnection
    setTimeout(
        function() {
            if (WebSocketInstance.state() === 1) {
                console.log('connection is secure')
                callback()
                return;
            } else {
                console.log('waiting for connection ....')
                waitForSocketConnection(callback, WebSocketInstance)
            } 
        }, 100);
}


export function renderMessages(messages) {
    const currentUser = this.props.username
    return messages.map(message => (
        <li
        key={message.id}
        className={message.author === currentUser ? 'sent': 'replies'}>
        <img src='http://emilcarlsson.se/assets/mikeross.png'/>
        <p>
            {message.content}
            <br/>
            <small>
                {message.timestamp.substring(0,16)}
            </small>
        </p>
        </li>
    ))
}

export const sendMessageHandler = (e, WebSocketInstance, props, message) =>{
    e.preventDefault();
    const messageObject = {
        from: props.name,
        content: message,
        chatID: props.ChatID
        
    }
    console.log('messageObject::', messageObject)
    if (messageObject.content) {
        console.log(messageObject.content)
        WebSocketInstance.newChatMessage(messageObject);

//         this.setState({
//             message: ''
//    })
    }
}

export function initializeChat(setMessage, addMessage, WebSocketInstance, props) {
    waitForSocketConnection(() => {
        WebSocketInstance.addCallbacks(
            setMessage,
            addMessage)
            //this.props.currentUser => daisuke
        WebSocketInstance.fetchMessages(props.name, props.ChatID)
    }, WebSocketInstance)
}
